.lc-menu{
	width: 345px;
	margin-bottom: 30px;
	ul{
		border-radius: 4px;
		li{
			a{
				padding: 15px 20px;
				font-size: 15px;
				line-height: 147%;
				border: 1px solid #CFD5D5;
				border-top: 0;
				display: flex;
				align-items: center;
				background-color: #F8FBFB;
			}
			&:first-child{
				a{
					border-top-left-radius: 4px;
					border-top-right-radius: 4px;
					border-top: 1px solid #CFD5D5;
				}

			}
			&:last-child{
				a{
					border-bottom-left-radius: 4px;
					border-bottom-right-radius: 4px;
				}

			}
			.sprite-svg{
				fill: #546062;
				margin-right: 20px;
			}
		}
		.active{
			position: relative;
			z-index: 10;
			a{
				font-weight: bold;
				background-color: #fff;
				box-shadow: 0px 0px 12px rgba(35, 41, 42, 0.1);
				border-bottom: transparent;
				.sprite-svg{
					fill: #F74B4B;
				}
			}
			&:last-child{
				a{
					border-bottom: 1px solid #CFD5D5;
				}
			}
		}
	}
}

